import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { common } from "@/assets/js/request.js";
Vue.use(Vuex)

const state = {
    hotelId: 2,
    HotelInfo: ''
}

const mutations = {
    // 
    storeHotel(state, Info) {
        state.HotelInfo = Info
    },
}

const actions = {
    getHotelInfo({ commit, state }) {
        return new Promise((resolve, reject) => {
            common.hotelInfo({
                Suffix: state.hotelId,
                fn: data => {
                    commit("storeHotel", data);
                    resolve()
                }
            });

        })
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions
})



